export default {
	name:"recommended",
	data() {
		return {
			recommended: [],
		}
	},
	created:function(e){
		uni.request({
			url: 'http://ceshi3.dishait.cn/api/goods/hotlist', 
			success: (res) => {
				this.recommended = res.data.data
			}
		});
	},
	methods: {
		details(e) {
			let id = e;
			console.log(id)
			uni.redirectTo({
				url: '../details/details?id=' + id,
				success(){
						 let page = getCurrentPages().pop(); 
				             if (!page) return;  
				             page.onLoad(id); 
					  }
			});
		}
	}
}
